import React, { Component } from "react";
import ReactHTMLTableToExcel from "react-html-table-to-excel";
import { getUserdata, localStoragedecryp } from "../../../../components/helpers/function";
class LaporanBarangEod extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

    render() {
        let total_berat_pejualan = 0;
        let total_harga_total_penjualan = 0;
        let total_jumlah_total_penjualan = 0;

        let total_berat_batal_penjualan = 0;
        let total_harga_total_batal_penjualan = 0;
        let total_jumlah_total_batal_penjualan = 0;

        let total_berat_pembelian = 0;
        let total_harga_total_pembelian = 0;
        let total_jumlah_total_pembelian = 0;
       
        let total_berat_batal_pembelian = 0;
        let total_harga_total_batal_pembelian = 0;
        let total_jumlah_total_batal_pembelian = 0;
     
        let total_berat_terima_barang = 0;
        let total_harga_total_terima_barang = 0;
        let total_jumlah_total_terima_barang = 0;

        let total_berat_pindah_barang = 0;
        let total_jumlah_pindah_barang = 0;

        let total_berat_hancur_barang = 0;
        let total_jumlah_hancur_barang = 0;

        let total_berat_saldo_barang = 0;
        let total_jumlah_saldo_barang = 0;

        this.props.data.forEach(element => {
            if (element.kategori === "PENJUALAN") {
               element.detail.forEach(detail => {
                total_berat_pejualan = total_berat_pejualan + detail.berat
                total_harga_total_penjualan = total_harga_total_penjualan + detail.harga_total
                total_jumlah_total_penjualan = total_jumlah_total_penjualan + detail.jumlah_total
              });
          }
            if (element.kategori === "BATAL PENJUALAN") {
               element.detail.forEach(detail => {
                total_berat_batal_penjualan = total_berat_batal_penjualan + detail.berat
                total_harga_total_batal_penjualan = total_harga_total_batal_penjualan + detail.harga_total
                total_jumlah_total_batal_penjualan = total_jumlah_total_batal_penjualan + detail.jumlah_total
              });
          }
            if (element.kategori === "PEMBELIAN") {
               element.detail.forEach(detail => {
                total_berat_pembelian = total_berat_pembelian + detail.berat
                total_harga_total_pembelian = total_harga_total_pembelian + detail.harga_total
                total_jumlah_total_pembelian = total_jumlah_total_pembelian + detail.jumlah_total
              });
          }
            if (element.kategori === "BATAL PEMBELIAN") {
               element.detail.forEach(detail => {
                total_berat_batal_pembelian = total_berat_batal_pembelian + detail.berat
                total_harga_total_batal_pembelian = total_harga_total_batal_pembelian + detail.harga_total
                total_jumlah_total_batal_pembelian = total_jumlah_total_batal_pembelian + detail.jumlah_total
              });
          }
            if (element.kategori === "TERIMA BARANG") {
               element.detail.forEach(detail => {
                total_berat_terima_barang = total_berat_terima_barang + detail.berat
                total_harga_total_terima_barang = total_harga_total_terima_barang + detail.harga_total
                total_jumlah_total_terima_barang = total_jumlah_total_terima_barang + detail.jumlah_total
              });
          }
            if (element.kategori === "PINDAH BARANG") {
               element.detail.forEach(detail => {
                total_berat_pindah_barang = total_berat_pindah_barang + detail.berat
                total_jumlah_pindah_barang = total_jumlah_pindah_barang + detail.jumlah_total
              });
          }
            if (element.kategori === "HANCUR BARANG") {
               element.detail.forEach(detail => {
                total_berat_hancur_barang = total_berat_hancur_barang + detail.berat
                total_jumlah_hancur_barang = total_jumlah_hancur_barang + detail.jumlah_total
              });
          }
            if (element.kategori === "SALDO BARANG") {
               element.detail.forEach(detail => {
                total_berat_saldo_barang = total_berat_saldo_barang + detail.berat
                total_jumlah_saldo_barang = total_jumlah_saldo_barang + detail.jumlah_total
              });
          }
      });
    return (
      <>
        <ReactHTMLTableToExcel
          id="test-table-xls-button"
          className="btn btn-success btn-block"
          table="table-to-xls"
          filename="Laporan EOD"
          sheet="Laporan EOD"
          buttonText="Export Exel"
        />
        <table
          rowkey="kode_member"
          id="table-to-xls"
          style={{ display: "none" }}
        >
          <thead>
            <tr>
              <th colSpan="8" style={{ textAlign: "center" }}> LAPORAN EOD
                </th>
            </tr>
            <tr>
              <th colSpan="8" style={{ textAlign: "center" }}>
                {" "}
                Tanggal{" "}
                {localStoragedecryp("tgl_laporan").length === 0
                  ? null
                  : JSON.parse(localStoragedecryp("tgl_laporan")).tanggal}{" "}
              </th>
            </tr>
            <tr>
              <th colSpan="8"> TOKO :  {getUserdata().nama_toko} </th>
            </tr>
           
            <tr>
              <th colSpan="8"> ALAMAT :  {getUserdata().alamat_toko} </th>
            </tr>
            <tr>
              <th colSpan="8" style={{ textAlign: "center" }}></th>
            </tr>
          </thead>

          {this.props.data.map((item, index) => (
            <>
                  <tbody>
                      {
                      item.kategori === "PENJUALAN" ?
                              <>
                                  {item.detail.length > 0 ?
                                      <>
                                            <tr>
                                                <th colSpan="8" style={{textAlign : "left"}}> KATEGORI : {item.kategori} </th>
                                            </tr>
                                            <tr>
                                                <th colSpan="2" style={{textAlign : "left" , backgroundColor: "#E8E5E5", color: "#000",}}> Kode Group </th>
                                                <th colSpan="2" style={{textAlign : "left" , backgroundColor: "#E8E5E5", color: "#000",}}> Berat </th>
                                                <th colSpan="2" style={{textAlign : "left" , backgroundColor: "#E8E5E5", color: "#000",}}> Harga Total </th>
                                                <th colSpan="2" style={{textAlign : "left" , backgroundColor: "#E8E5E5", color: "#000",}}> Jumlah Total </th>
                                            </tr>
                                            {item.detail.map((detail, index) => (
                                                <tr>
                                                    <td colSpan="2" style={{textAlign : "left"}}> { detail._id.kode_group } </td>
                                                    <td colSpan="2" style={{textAlign : "left"}}> { detail.berat } </td>
                                                    <td colSpan="2" style={{textAlign : "right"}}> { detail.harga_total } </td>
                                                    <td colSpan="2" style={{textAlign : "left"}}> { detail.jumlah_total } </td>
                                                </tr>
                                            ))}
                                        <tr>
                                            <td colSpan="2" style={{textAlign : "center"}}> Total </td>
                                            <td colSpan="2" style={{textAlign : "left"}}> {total_berat_pejualan} </td>
                                            <td colSpan="2" style={{textAlign : "right"}}> {total_harga_total_penjualan} </td>
                                            <td colSpan="2" style={{textAlign : "left"}}> {total_jumlah_total_penjualan} </td>
                                    </tr> 
                                </>:null
                              }
                          </>
                          :  item.kategori === "BATAL PENJUALAN" ?
                        <>
                            {item.detail.length > 0 ? <>
                                <tr>
                                    <th colSpan="8" style={{ textAlign: "left" }}> KATEGORI : {item.kategori} </th>
                                </tr>
                                <tr>
                                    <th colSpan="2" style={{ textAlign: "left", backgroundColor: "#E8E5E5", color: "#000",  }}> Kode Group </th>
                                    <th colSpan="2" style={{ textAlign: "left", backgroundColor: "#E8E5E5", color: "#000",  }}> Berat </th>
                                    <th colSpan="2" style={{ textAlign: "left", backgroundColor: "#E8E5E5", color: "#000",  }}> Harga Total </th>
                                    <th colSpan="2" style={{ textAlign: "left", backgroundColor: "#E8E5E5", color: "#000",  }}> Jumlah Total </th>
                                </tr>
                                {item.detail.map((detail, index) => (
                                    <tr>
                                        <td colSpan="2" style={{  textAlign: "left" }}> {detail._id.kode_group} </td>
                                        <td colSpan="2" style={{  textAlign: "left" }}> {detail.berat} </td>
                                        <td colSpan="2" style={{  textAlign: "right" }}> {detail.harga_total} </td>
                                        <td colSpan="2" style={{  textAlign: "left" }}> {detail.jumlah_total} </td>
                                    </tr>
                                ))}
                                <tr>
                                    <td colSpan="2" style={{  textAlign: "center" }}> Total </td>
                                    <td colSpan="2" style={{  textAlign: "left" }}> {total_berat_batal_penjualan} </td>
                                    <td colSpan="2" style={{  textAlign: "right" }}> {total_harga_total_batal_penjualan} </td>
                                    <td colSpan="2" style={{  textAlign: "left" }}> {total_jumlah_total_batal_penjualan} </td>
                                </tr>
                            </> : null}
                          </>
                          :  item.kategori === "PEMBELIAN" ?
                            <>
                            {item.detail.length > 0 ? <>
                                <tr>
                                    <th colSpan="8" style={{ textAlign: "left" }}> KATEGORI : {item.kategori} </th>
                                </tr>
                                <tr>
                                    <th colSpan="2" style={{ textAlign: "left", backgroundColor: "#E8E5E5", color: "#000",  }}> Kode Dept </th>
                                    <th colSpan="2" style={{ textAlign: "left", backgroundColor: "#E8E5E5", color: "#000",  }}> Berat </th>
                                    <th colSpan="2" style={{ textAlign: "left", backgroundColor: "#E8E5E5", color: "#000",  }}> Harga Total </th>
                                    <th colSpan="2" style={{ textAlign: "left", backgroundColor: "#E8E5E5", color: "#000",  }}> Jumlah Total </th>
                                </tr>
                                {item.detail.map((detail, index) => (
                                    <tr>
                                        <td colSpan="2" style={{  textAlign: "left" }}> {detail._id.kode_dept} </td>
                                        <td colSpan="2" style={{  textAlign: "left" }}> {detail.berat} </td>
                                        <td colSpan="2" style={{  textAlign: "right" }}> {detail.harga_total} </td>
                                        <td colSpan="2" style={{  textAlign: "left" }}> {detail.jumlah_total} </td>
                                    </tr>
                                ))}
                                <tr>
                                    <td colSpan="2" style={{  textAlign: "center" }}> Total </td>
                                    <td colSpan="2" style={{  textAlign: "left" }}> {total_berat_pembelian} </td>
                                    <td colSpan="2" style={{  textAlign: "right" }}> {total_harga_total_pembelian} </td>
                                    <td colSpan="2" style={{  textAlign: "left" }}> {total_jumlah_total_pembelian} </td>
                                </tr>
                            </> : null}
                          </>
                          : item.kategori === "BATAL PEMBELIAN" ?
                        <>
                        {item.detail.length > 0 ? <>
                            <tr>
                                <th colSpan="8" style={{ textAlign: "left" }}> KATEGORI : {item.kategori} </th>
                            </tr>
                            <tr>
                                <th colSpan="2" style={{ textAlign: "left", backgroundColor: "#E8E5E5", color: "#000",  }}> Kode Dept </th>
                                <th colSpan="2" style={{ textAlign: "left", backgroundColor: "#E8E5E5", color: "#000",  }}> Berat </th>
                                <th colSpan="2" style={{ textAlign: "left", backgroundColor: "#E8E5E5", color: "#000",  }}> Harga Total </th>
                                <th colSpan="2" style={{ textAlign: "left", backgroundColor: "#E8E5E5", color: "#000",  }}> Jumlah Total </th>
                            </tr>
                            {item.detail.map((detail, index) => (
                                <tr>
                                    <td colSpan="2" style={{  textAlign: "left" }}> {detail._id.kode_dept} </td>
                                    <td colSpan="2" style={{  textAlign: "left" }}> {detail.berat} </td>
                                    <td colSpan="2" style={{  textAlign: "right" }}> {detail.harga_total} </td>
                                    <td colSpan="2" style={{  textAlign: "left" }}> {detail.jumlah_total} </td>
                                </tr>
                            ))}
                                 <tr>
                                    <td colSpan="2" style={{  textAlign: "center" }}> Total </td>
                                    <td colSpan="2" style={{  textAlign: "left" }}> {total_berat_batal_pembelian} </td>
                                    <td colSpan="2" style={{  textAlign: "right" }}> {total_harga_total_batal_pembelian} </td>
                                    <td colSpan="2" style={{  textAlign: "left" }}> {total_jumlah_total_batal_pembelian} </td>
                                </tr>
                        </> : null}
                          </>
                          : item.kategori === "TERIMA BARANG" ?
                            <>
                                {item.detail.length > 0 ? <>
                                    <tr>
                                        <th colSpan="8" style={{ textAlign: "left" }}> KATEGORI : {item.kategori} </th>
                                    </tr>
                                    <tr>
                                        <th colSpan="2" style={{ textAlign: "left", backgroundColor: "#E8E5E5", color: "#000",  }}> Kode Group </th>
                                        <th colSpan="2" style={{ textAlign: "left", backgroundColor: "#E8E5E5", color: "#000",  }}> Kode Toko </th>
                                        <th colSpan="2" style={{ textAlign: "left", backgroundColor: "#E8E5E5", color: "#000",  }}> Berat </th>
                                        <th colSpan="2" style={{ textAlign: "left", backgroundColor: "#E8E5E5", color: "#000",  }}> Jumlah Total </th>
                                    </tr>
                                    {item.detail.map((detail, index) => (
                                        <tr>
                                            <td colSpan="2" style={{  textAlign: "left" }}> {detail._id.kode_group} </td>
                                            <td colSpan="2" style={{  textAlign: "left" }}> {detail._id.kode_toko} </td>
                                            <td colSpan="2" style={{  textAlign: "left" }}> {detail.berat} </td>
                                            <td colSpan="2" style={{  textAlign: "left" }}> {detail.jumlah_total} </td>
                                        </tr>
                                    ))}
                                <tr>
                                    <td colSpan="4" style={{  textAlign: "center" }}> Total </td>
                                    <td colSpan="2" style={{  textAlign: "left" }}> {total_berat_terima_barang} </td>
                                    <td colSpan="2" style={{  textAlign: "left" }}> {total_jumlah_total_terima_barang} </td>
                                </tr>
                                </> : null}
                          </>
                          : item.kategori === "PINDAH BARANG" ?
                            <>
                            {item.detail.length > 0 ? <>
                                <tr>
                                    <th colSpan="8" style={{ textAlign: "left" }}> KATEGORI : {item.kategori} </th>
                                </tr>
                                <tr>
                                    <th style={{ textAlign: "left", backgroundColor: "#E8E5E5", color: "#000" }}> Kode Group </th>
                                    <th style={{ textAlign: "left", backgroundColor: "#E8E5E5", color: "#000" }}> Kode Gudang Asal </th>
                                    <th style={{ textAlign: "left", backgroundColor: "#E8E5E5", color: "#000" }}> Kode Baki Asal </th>
                                    <th style={{ textAlign: "left", backgroundColor: "#E8E5E5", color: "#000" }}> Kode Gudang </th>
                                    <th style={{ textAlign: "left", backgroundColor: "#E8E5E5", color: "#000" }}> Kode Baki </th>
                                    <th style={{ textAlign: "left", backgroundColor: "#E8E5E5", color: "#000" }}> Berat </th>
                                    <th colSpan="2" style={{ textAlign: "left", backgroundColor: "#E8E5E5", color: "#000" }}> Jumlah Total </th>
                                </tr>
                                {item.detail.map((detail, index) => (
                                    <tr>
                                        <td style={{  textAlign: "left" }}> {detail._id.kode_group} </td>
                                        <td style={{  textAlign: "left" }}> {detail._id.kode_gudang_asal} </td>
                                        <td style={{  textAlign: "left" }}> {detail._id.kode_baki_asal} </td>
                                        <td style={{  textAlign: "left" }}> {detail._id.kode_gudang} </td>
                                        <td style={{  textAlign: "left" }}> {detail._id.kode_baki} </td>
                                        <td style={{  textAlign: "left" }}> {detail.berat} </td>
                                        <td colSpan="2" style={{  textAlign: "left" }}> {detail.jumlah_total} </td>
                                    </tr>
                                ))}
                                <tr>
                                    <td colspan="5" style={{  textAlign: "center" }}> Total </td>
                                    <td style={{  textAlign: "left" }}> {total_berat_pindah_barang} </td>                          
                                    <td style={{  textAlign: "left" }} colSpan="2"> {total_jumlah_pindah_barang} </td>                          
                                </tr>
                            </> : null}
                          </>
                          : item.kategori === "HANCUR BARANG" ?
                            <>
                            {item.detail.length > 0 ? <>
                                <tr>
                                    <th colSpan="8" style={{ textAlign: "left" }}> KATEGORI : {item.kategori} </th>
                                </tr>
                                <tr>
                                    <th colSpan="3" style={{ textAlign: "left", backgroundColor: "#E8E5E5", color: "#000" }}> Kode Group </th>
                                    <th colSpan="3" style={{ textAlign: "left", backgroundColor: "#E8E5E5", color: "#000" }}> Berat </th>
                                    <th colSpan="2" style={{ textAlign: "left", backgroundColor: "#E8E5E5", color: "#000" }}> Jumlah Total </th>
                                </tr>
                                {item.detail.map((detail, index) => (
                                    <tr>
                                        <td colSpan="3" style={{  textAlign: "left" }}> {detail._id.kode_group} </td>
                                        <td colSpan="3" style={{  textAlign: "left" }}> {detail.berat} </td>
                                        <td colSpan="2" style={{  textAlign: "left" }}> {detail.jumlah_total} </td>
                                    </tr>
                                ))}
                                <tr>
                                    <td colSpan="3" style={{  textAlign: "center" }} > Total </td>                          
                                    <td colSpan="3" style={{  textAlign: "left" }}> {total_berat_hancur_barang} </td>                          
                                    <td colSpan="2" style={{  textAlign: "left" }}> {total_jumlah_hancur_barang} </td>                          
                                </tr>
                            </> : null}
                          </>
                          : item.kategori === "SALDO BARANG" ?
                            <>
                            {item.detail.length > 0 ? <>
                                <tr>
                                    <th colSpan="8" style={{ textAlign: "left" }}> KATEGORI : {item.kategori} </th>
                                </tr>
                                <tr>
                                    <th colSpan="3" style={{ textAlign: "left", backgroundColor: "#E8E5E5", color: "#000",  }}> Kode Group </th>
                                    <th colSpan="3" style={{ textAlign: "left", backgroundColor: "#E8E5E5", color: "#000",  }}> Berat </th>
                                    <th colSpan="2" style={{ textAlign: "left", backgroundColor: "#E8E5E5", color: "#000",  }}> Jumlah Total </th>
                                </tr>
                                {item.detail.map((detail, index) => (
                                    <tr>
                                        <td colSpan="3" style={{  textAlign: "left" }}> {detail._id.kode_group} </td>
                                        <td colSpan="3" style={{  textAlign: "left" }}> {detail.berat} </td>
                                        <td colSpan="2" style={{  textAlign: "left" }}> {detail.jumlah_total} </td>
                                    </tr>
                                ))}
                                <tr>
                                    <td colSpan="3" style={{  textAlign: "center" }}> Total </td>
                                    <td colSpan="3" style={{  textAlign: "left" }}> {total_berat_saldo_barang} </td>
                                    <td colSpan="2" style={{  textAlign: "left" }}> {total_jumlah_saldo_barang} </td>
                                </tr>
                            </> : null}
                          </>
                          : null
                          }</tbody>
            </>
          ))}
          <tfoot></tfoot>
        </table>
      </>
    );
  }
}
export default LaporanBarangEod;
