import React, { Component } from "react";

import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { InputDate } from "../../../components/helpers/field";
import { getTglSystem } from "../../../components/helpers/function";
import Tabel from "../../../components/helpers/tabel";
import ValidasiTanggalLaporan from "../../../Validasi/ValidasiTanggalLaporan";
import LaporanBarangEod from "./exel/LaporanBarangEod";
import CetakLaporanEod from "./pdf/CetakLaporanEod";
import Loading from 'react-fullscreen-loading';

class FilterLaporanEod extends Component {
  constructor(props) {
    super(props);
    this.state = {
      date: new Date(),
    };
    // this.handleSubmit = this.handleSubmit.bind(this);
    this.setStartDate = this.setStartDate.bind(this);
    this.setLastDate = this.setLastDate.bind(this);
  }

  exportPdf() {
    CetakLaporanEod(this.props.LaporanEod);
  }
  setStartDate(date) {
    this.setState({
      tgl_awal: new Date(date),
    });
  }
  setLastDate(date) {
    this.setState({
      tgl_akhir: new Date(date),
    });
  }

  render() {
    let penjualan = [];
    let batalpenjualan = [];
    let pembelian = [];
    let batalpembelian = [];
    let terimabarang = [];
    let pindahbarang = [];
    let hancurbarang = [];
    let saldobarang = [];
    this.props.LaporanEod.forEach((element) => {
      element.detail.forEach((hasil) => {
        if (element.kategori === "BATAL PENJUALAN") {
          let row = {
            kategori: element.kategori,
            kode_group: hasil._id.kode_group,
            berat: hasil.berat,
            harga_total:
              hasil.harga_total === undefined ? "0" : hasil.harga_total,
            jumlah_total: hasil.jumlah_total,
          };
          batalpenjualan.push(row);
        }
        if (element.kategori === "PENJUALAN") {
          let row = {
            kategori: element.kategori,
            kode_group: hasil._id.kode_group,
            berat: hasil.berat,
            harga_total:
              hasil.harga_total === undefined ? "0" : hasil.harga_total,
            jumlah_total: hasil.jumlah_total,
          };
          penjualan.push(row);
        }
        if (element.kategori === "PEMBELIAN") {
          let row = {
            kategori: element.kategori,
            kode_dept: hasil._id.kode_dept,
            berat: hasil.berat,
            harga_total:
              hasil.harga_total === undefined ? "0" : hasil.harga_total,
            jumlah_total: hasil.jumlah_total,
          };
          pembelian.push(row);
        }
        if (element.kategori === "BATAL PEMBELIAN") {
          let row = {
            kategori: element.kategori,
            kode_dept: hasil._id.kode_dept,
            berat: hasil.berat,
            harga_total:
              hasil.harga_total === undefined ? "0" : hasil.harga_total,
            jumlah_total: hasil.jumlah_total,
          };
          batalpembelian.push(row);
        }
        if (element.kategori === "TERIMA BARANG") {
          let row = {
            kategori: element.kategori,
            kode_group: hasil._id.kode_group,
            kode_toko: hasil._id.kode_toko,
            berat: hasil.berat,
            jumlah_total: hasil.jumlah_total,
          };
          terimabarang.push(row);
        }
        if (element.kategori === "PINDAH BARANG") {
          let row = {
            kategori: element.kategori,
            kode_group: hasil._id.kode_group,
            kode_gudang_asal: hasil._id.kode_gudang_asal,
            kode_baki_asal: hasil._id.kode_baki_asal,
            kode_gudang: hasil._id.kode_gudang,
            kode_baki: hasil._id.kode_baki,
            berat: hasil.berat,
            jumlah_total: hasil.jumlah_total,
          };
          pindahbarang.push(row);
        }
        if (element.kategori === "HANCUR BARANG") {
          let row = {
            kategori: element.kategori,
            kode_group: hasil._id.kode_group,
            berat: hasil.berat,
            jumlah_total: hasil.jumlah_total,
          };
          hancurbarang.push(row);
        }
        if (element.kategori === "SALDO BARANG") {
          let row = {
            kategori: element.kategori,
            kode_group: hasil._id.kode_group,
            berat: hasil.berat,
            jumlah_total: hasil.jumlah_total,
          };
          saldobarang.push(row);
        }
      });
      // databarang.push(element);
    });

    const columns_penjualan = [
      {
        dataField: "kategori",
        text: "Kategori",
        footer: "",
      },
      {
        dataField: "kode_group",
        text: "Kode Group",
        footer: "",
      },

      {
        dataField: "berat",
        text: "Berat",
        formatter: (data) => {
          return (
            <div>
              {" "}
              {data === undefined || null
                ? 0
                : data.toFixed(3)}
            </div>
          );
        },
        footer: (columnData) => (
          <div>
            {" "}
            {columnData.reduce((acc, item) => acc + item, 0).toFixed(3)}{" "}
          </div>
        ),
      },

      {
        dataField: "harga_total",
        text: "Harga Total",
        headerClasses: "text-right",
        formatter: (data) => {
          return (
            <div className="text-right">
              {" "}
              {data === undefined || null
                ? 0
                : parseFloat(data).toLocaleString("kr-KO")}
            </div>
          );
        },
        footer: (columnData) => (
          <div className="text-right">
            {" "}
            {parseInt(
              columnData.reduce((acc, item) => acc + item, 0)
            ).toLocaleString("kr-KO")}{" "}
          </div>
        ),
      },
      {
        dataField: "jumlah_total",
        text: "Jumlah Total",
        headerClasses: "text-right",
        formatter: (data) => {
          return (
            <div className="text-right">
              {" "}
              {data === undefined || null
                ? 0
                : parseFloat(data).toLocaleString("kr-KO")}
            </div>
          );
        },
        footer: (columnData) => (
          <div className="text-right">
            {" "}
            {parseInt(
              columnData.reduce((acc, item) => acc + item, 0)
            ).toLocaleString("kr-KO")}{" "}
          </div>
        ),
      },
    ];
    const columns_pembelian = [
      {
        dataField: "kategori",
        text: "Kategori",
        footer: "",
      },
      {
        dataField: "kode_dept",
        text: "Kode Dept",
        footer: "",
      },

      {
        dataField: "berat",
        text: "Berat",
        formatter: (data) => {
          return (
            <div>
              {" "}
              {data === undefined || null
                ? 0
                : data.toFixed(3)}
            </div>
          );
        },
        footer: (columnData) => (
          <div>
            {" "}
            {columnData.reduce((acc, item) => acc + item, 0).toFixed(3)}{" "}
          </div>
        ),
      },

      {
        dataField: "harga_total",
        text: "Harga Total",
        headerClasses: "text-right",
        formatter: (data) => {
          return (
            <div className="text-right">
              {" "}
              {data === undefined || null
                ? 0
                : parseFloat(data).toLocaleString("kr-KO")}
            </div>
          );
        },
        footer: (columnData) => (
          <div className="text-right">
            {" "}
            {parseInt(
              columnData.reduce((acc, item) => acc + item, 0)
            ).toLocaleString("kr-KO")}{" "}
          </div>
        ),
      },
      {
        dataField: "jumlah_total",
        text: "Jumlah Total",
        headerClasses: "text-right",
        formatter: (data) => {
          return (
            <div className="text-right">
              {" "}
              {data === undefined || null
                ? 0
                : parseFloat(data).toLocaleString("kr-KO")}
            </div>
          );
        },
        footer: (columnData) => (
          <div className="text-right">
            {" "}
            {parseInt(
              columnData.reduce((acc, item) => acc + item, 0)
            ).toLocaleString("kr-KO")}{" "}
          </div>
        ),
      },
    ];
    const columns_terima_barang = [
      {
        dataField: "kategori",
        text: "Kategori",
        footer: "",
      },
      {
        dataField: "kode_group",
        text: "Kode Group",
        footer: "",
      },
      {
        dataField: "kode_toko",
        text: "Kode Toko",
        footer: "",
      },

      {
        dataField: "berat",
        text: "Berat",
        formatter: (data) => {
          return (
            <div>
              {" "}
              {data === undefined || null
                ? 0
                : data.toFixed(3)}
            </div>
          );
        },
        footer: (columnData) => (
          <div>
            {" "}
            {columnData.reduce((acc, item) => acc + item, 0).toFixed(3)}{" "}
          </div>
        ),
      },
      {
        dataField: "jumlah_total",
        text: "Jumlah Total",
        headerClasses: "text-right",
        formatter: (data) => {
          return (
            <div className="text-right">
              {" "}
              {data === undefined || null
                ? 0
                : parseFloat(data).toLocaleString("kr-KO")}
            </div>
          );
        },
        footer: (columnData) => (
          <div className="text-right">
            {" "}
            {parseInt(
              columnData.reduce((acc, item) => acc + item, 0)
            ).toLocaleString("kr-KO")}{" "}
          </div>
        ),
      },
    ];
    const columns_pindah_barang = [
      {
        dataField: "kategori",
        text: "Kategori",
        footer: "",
      },
      {
        dataField: "kode_group",
        text: "Kode Group",
        footer: "",
      },
      {
        dataField: "kode_gudang_asal",
        text: "Gudang Asal",
        footer: "",
      },
      {
        dataField: "kode_baki_asal",
        text: "Baki Asal",
        footer: "",
      },
      {
        dataField: "kode_gudang",
        text: "Kode Gudang",
        footer: "",
      },
      {
        dataField: "kode_baki",
        text: "Kode Baki",
        footer: "",
      },

      {
        dataField: "berat",
        text: "Berat",
        formatter: (data) => {
          return (
            <div>
              {" "}
              {data === undefined || null
                ? 0
                : data.toFixed(3)}
            </div>
          );
        },
        footer: (columnData) => (
          <div>
            {" "}
            {columnData.reduce((acc, item) => acc + item, 0).toFixed(3)}{" "}
          </div>
        ),
      },
      {
        dataField: "jumlah_total",
        text: "Jumlah Total",
        headerClasses: "text-right",
        formatter: (data) => {
          return (
            <div className="text-right">
              {" "}
              {data === undefined || null
                ? 0
                : parseFloat(data).toLocaleString("kr-KO")}
            </div>
          );
        },
        footer: (columnData) => (
          <div className="text-right">
            {" "}
            {parseInt(
              columnData.reduce((acc, item) => acc + item, 0)
            ).toLocaleString("kr-KO")}{" "}
          </div>
        ),
      },
    ];
    const columns_hancur_barang = [
      {
        dataField: "kategori",
        text: "Kategori",
        footer: "",
      },
      {
        dataField: "kode_group",
        text: "Kode Group",
        footer: "",
      },
      {
        dataField: "berat",
        text: "Berat",
        formatter: (data) => {
          return (
            <div>
              {" "}
              {data === undefined || null
                ? 0
                : data.toFixed(3)}
            </div>
          );
        },
        footer: (columnData) => (
          <div>
            {" "}
            {columnData.reduce((acc, item) => acc + item, 0).toFixed(3)}{" "}
          </div>
        ),
      },
      {
        dataField: "jumlah_total",
        text: "Jumlah Total",
        headerClasses: "text-right",
        formatter: (data) => {
          return (
            <div className="text-right">
              {" "}
              {data === undefined || null
                ? 0
                : parseFloat(data).toLocaleString("kr-KO")}
            </div>
          );
        },
        footer: (columnData) => (
          <div className="text-right">
            {" "}
            {parseInt(
              columnData.reduce((acc, item) => acc + item, 0)
            ).toLocaleString("kr-KO")}{" "}
          </div>
        ),
      },
    ];
    return (
      <form onSubmit={this.props.handleSubmit} autoComplete="off">
        <div className="row">
          <div className="col-lg-6">
            <Field
              name="tgl_awal"
              component={InputDate}
              label="Tanggal Dari"
              type="text"
              defaultValue={getTglSystem()}
              selected={this.state.tgl_awal}
              onChange={(date) => this.setStartDate(date)}
              placeholder="Masukan Tanggal Dari"
            />
          </div>

          <div className="col-lg-6">
            <div className="text-right">
              <label>&nbsp;</label>
              <button
                type="submit"
                value="PDF"
                name="type_btn"
                className="btn btn-primary btn-block"
              >
                {this.props.isLoading ? (
                  <>
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                    Mencari
                  </>
                ) : (
                  "Cari Laporan"
                )}
              </button>
            </div>
          </div>
          <div className="col-12">
            <Tabel
              keyField="kode_group"
              data={this.props.export ? penjualan : []}
              columns={columns_penjualan}
              empty={true}
              textEmpty="Data Laporan EOD Penjualan Kosong"
            />
          </div>
          <div className="col-12">
            <Tabel
              keyField="kode_group"
              data={this.props.export ? batalpenjualan : []}
              columns={columns_penjualan}
              empty={true}
              textEmpty="Data Laporan EOD Batal Penjualan Kosong"
            />
        </div>
          <div className="col-12">
            <Tabel
              keyField="kode_dept"
              data={this.props.export ? pembelian : []}
              columns={columns_pembelian}
              empty={true}
              textEmpty="Data Laporan EOD Pembelian Kosong"
            />
          </div>
          <div className="col-12">
            <Tabel
              keyField="kode_dept"
              data={this.props.export ? batalpembelian : []}
              columns={columns_pembelian}
              empty={true}
              textEmpty="Data Laporan EOD Batal Pembelian Kosong"
            />
          </div>
          <div className="col-12">
            <Tabel
              keyField="kode_group"
              data={this.props.export ? terimabarang : []}
              columns={columns_terima_barang}
              empty={true}
              textEmpty="Data Laporan EOD Terima Barang Kosong"
            />
          </div>
          <div className="col-12">
            <Tabel
              keyField="kode_group"
              data={this.props.export ? pindahbarang : []}
              columns={columns_pindah_barang}
              empty={true}
              textEmpty="Data Laporan EOD Pindah Barang Kosong"
            />
          </div>
          <div className="col-12">
            <Tabel
              keyField="kode_group"
              data={this.props.export ? hancurbarang : []}
              columns={columns_hancur_barang}
              empty={true}
              textEmpty="Data Laporan EOD Hancur Barang Kosong"
            />
          </div>
          <div className="col-12">
            <Tabel
              keyField="kode_group"
              data={this.props.export ? saldobarang : []}
              columns={columns_hancur_barang}
              empty={true}
              textEmpty="Data Laporan EOD Saldo Barang Kosong"
            />
          </div>
          <div className={this.props.export ? "col-lg-12" : "col-lg-12 d-none"}>
            {/* <label htmlFor="">&nbsp;</label> */}
            <div className="row">
              <div className="col-lg-6">
                <button
                  onClick={() => this.exportPdf()}
                  className="btn btn-warning btn-block"
                >
                  {" "}
                  Export PDF{" "}
                </button>
              </div>
              <div className="col-lg-6">
                <LaporanBarangEod data={this.props.LaporanEod} />
              </div>
            </div>
          </div>
        </div>
        {this.props.isLoading ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null}
      </form>
    );
  }
}

FilterLaporanEod = reduxForm({
  form: "FilterLaporanEod",
  enableReinitialize: true,
  validate: ValidasiTanggalLaporan,
})(FilterLaporanEod);
export default connect((state) => {
  return {
    initialValues: {
      tgl_awal: getTglSystem(),
      // tgl_akhir: getTglSystem(),
    },
  };
})(FilterLaporanEod);
